export const data = (that) => {
  return {
    // 下拉选择选项配置
    dropdown: {
      // 品牌
      model: [
        {
          value: 0,
          label: '全部'
        },
        {
          value: 1,
          label: '上柴'
        },
        {
          value: 2,
          label: '潍柴'
        },
        {
          value: 3,
          label: '玉柴'
        },
        {
          value: 4,
          label: '孚创柴'
        }
      ],
      // 系列
      seriesList: [],
      // 策略主备分类枚举
      strategyType: [
        {
          value: 1,
          label: '备用机'
        },
        {
          value: 2,
          label: '主用机'
        }
      ],
      strategyState: [
        {
          value: 0,
          label: '全部'
        },
        {
          value: 2,
          label: '启用'
        },
        {
          value: 1,
          label: '禁用'
        }
      ]
    },
    // 自定义表格相关
    table: {
      columns: [
        {
          label: '维保策略名称',
          prop: 'StrategyName',
          minWidth: '15%'
        },
        {
          label: '发动机品牌',
          prop: 'EngineBrandName',
          minWidth: '15%'
        },
        {
          label: '发动机系列',
          prop: 'EngineSeriesName',
          minWidth: '15%'
        },
        {
          label: '策略主备分类',
          prop: 'Type',
          minWidth: '15%',
          formatter: (row, column, index) => {
            return row.Type
              ? that.dropdown.strategyType.find((item) => item.value == row.Type).label
              : ''
          }
        },
        {
          label: '策略主备状态',
          prop: 'IsUsing',
          minWidth: '15%',
          formatter: (row, column, index) => {
            return row.IsUsing ? '启用' : '禁用'
          }
        },
        {
          label: '最后编辑人',
          prop: 'UpDateUserName',
          minWidth: '15%'
        },
        {
          label: '最后编辑时间',
          prop: 'UpDateTime',
          minWidth: '20%',
          formatter: (row, column, index) => {
            return row.UpDateTime ? that.$moment(row.UpDateTime).format('yyyy-MM-DD HH:mm:ss') : ''
          }
        }
      ],
      dataSource: [
        {
          name: '上柴E系列维保方案',
          brand: '上柴',
          model: 'E',
          type: '备用机',
          state: '启用',
          lastEditor: '李娜',
          lastEditorTime: '2022-3-11',
          rawData: {
            ID: 1,
            name: '上柴策略A',
            model: '上柴',
            series: 'E',
            state: '启用',
            beatDuration: 8,
            beatTotal: 60,
            periodDuration: '8*60',
            periodTotal: 4,
            duration: '8*60*4',
            type: '备用机',
            items: [
              {
                name: '维保事项名字',
                operatingTime: '120小时',
                absoluteTime: '5天',
                lastEditTime: '2022-3-15 11:12:18',
                username: '张三'
              }
            ]
          }
        }
      ]
    },
    // 可编辑表格数据
    editTable: {
      table_columns: [
        {
          prop: 'ItemContent',
          label: '维保事项',
          width: '100'
        },
        {
          prop: 'DefaultHourSetting',
          label: '运行时间',
          width: '100'
        },
        {
          prop: 'DefaultDaySetting',
          label: '绝对时间',
          width: '100'
        }
      ],
      dataArray: []
    }
  }
}



