<template>
  <div>
    <BMainTopComponent :title="title" />
    <!-- 
      :isShowBackBtn="true"
      :isHandleBackClick="true"
      @handleBackClick="handleBackClick" -->
    <div class="maintenance-strategy-add">
      <el-row class="title" type="flex" align="middle">
        <el-col :span="12">
          <el-row type="flex" justify="start">维保策略</el-row>
        </el-col>
        <el-col :span="12">
          <el-row type="flex" justify="end">
            <el-popconfirm
              confirm-button-text="是"
              cancel-button-text="否"
              icon="el-icon-info"
              icon-color="red"
              title="您所做的更改可能未保存,是否确定返回？"
              @confirm="cancel"
              ><el-button type="info" plain class="cancel-btn" size="mini" slot="reference"
                >取消</el-button
              ></el-popconfirm
            >
            <el-button type="primary" class="save-btn" size="mini" @click="handleSave()"
              >保存</el-button
            >
          </el-row>
        </el-col>
      </el-row>
      <el-row class="base-info">
        <el-col :span="8">
          <el-row type="flex" align="middle">
            <label
              ><span>维保策略名称：</span
              ><el-input
                v-model="form.StrategyName"
                maxlength="20"
                type="text"
                placeholder="请输入"
                show-word-limit
              ></el-input></label
          ></el-row>
        </el-col>
        <el-col :span="8">
          <el-row type="flex" align="middle">
            <label>
              <span>发动机品牌：</span>
              <el-select
                v-model="form.EngineBrandId"
                placeholder="请选择"
                @change="EngineBrandChange()"
              >
                <el-option
                  v-for="item in dropdown.model"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                >
                </el-option>
              </el-select>
            </label>
          </el-row>
        </el-col>
        <el-col :span="8">
          <el-row type="flex" align="middle">
            <label>
              <span>发动机系列：</span>
              <el-select v-model="form.EngineSeriesId" placeholder="请选择">
                <el-option
                  v-for="item in dropdown.seriesList"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                >
                </el-option>
              </el-select>
              <!-- <el-input
                maxlength="15"
                show-word-limit
                v-model="form.series"
                placeholder="请输入"
              ></el-input> -->
            </label>
          </el-row>
        </el-col>
        <el-col :span="8">
          <el-row type="flex" align="middle">
            <label
              ><span>节拍时长：</span>
              <el-input-number
                controls-position="right"
                :min="0"
                :max="100"
                v-model="form.BeatLength"
                placeholder="请输入"
              ></el-input-number>
              <!-- <el-input v-model="form.beatDuration" placeholder="请输入"></el-input> -->
            </label></el-row
          >
        </el-col>
        <el-col :span="8">
          <el-row type="flex" align="middle">
            <label
              ><span>周期节拍个数：</span>
              <el-input-number
                controls-position="right"
                :min="0"
                :max="100"
                v-model="form.BeatCount"
                placeholder="请输入"
              ></el-input-number> </label
          ></el-row>
        </el-col>
        <el-col :span="8">
          <el-row type="flex" align="middle">
            <label
              ><span>维保周期时长：</span>
              <el-input disabled v-model="cycleDuration" placeholder="请输入"> </el-input></label
          ></el-row>
        </el-col>
        <el-col :span="8">
          <el-row type="flex" align="middle">
            <label
              ><span>维保周期个数：</span>
              <el-input-number
                :min="0"
                :max="20"
                controls-position="right"
                v-model="form.CycleCount"
                placeholder="请输入"
              ></el-input-number> </label
          ></el-row>
        </el-col>
        <el-col :span="8">
          <el-row type="flex" align="middle">
            <label
              ><span>检修周期时长：</span>
              <el-input disabled v-model="totalDuration" placeholder="请输入"></el-input></label
          ></el-row>
        </el-col>
        <el-col :span="8">
          <el-row type="flex" align="middle">
            <label
              ><span>策略主备分类：</span>
              <el-select v-model="form.Type">
                <el-option
                  v-for="item in dropdown.strategyType"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                >
                </el-option>
              </el-select> </label
          ></el-row>
        </el-col>
        <el-col :span="8">
          <el-row type="flex" align="middle">
            <label label-last><span>维保事项：</span></label></el-row
          >
        </el-col>
      </el-row>
      <el-row class="items">
        <edit-table
          :table_columns="table_columns"
          :dataArray="form.StrategyItemList"
          @dataChange="tableDataChange"
        />
      </el-row>
    </div>
  </div>
</template>

<script>
import EditTable from './components/EditTable/index.vue'
import { data as _data } from './data'

export default {
  name: 'MaintenanceStrategyAdd',
  components: {
    EditTable
  },
  data() {
    return {
      table_columns: _data(this).editTable.table_columns,
      dropdown: _data(this).dropdown,
      form: {
        StrategyName: '',
        EngineBrandId: null,
        EngineSeriesId: null,
        Status: null,
        // BeatLength: '',
        // BeatCount: '',
        // CycleCount: '',
        Type: null,
        StrategyItemList: []
      }
    }
  },
  computed: {
    title() {
      let str = this.$route.query && this.$route.query.form ? '编辑' : '新增'
      return `${str}维保策略（新）`
    },
    cycleDuration() {
      if (!!this.form.BeatLength && !!this.form.BeatCount) {
        return this.form.BeatLength * this.form.BeatCount
      }
      return null
    },
    totalDuration() {
      if (!!this.cycleDuration && !!this.form.CycleCount) {
        return this.cycleDuration * this.form.CycleCount
      }
      return null
    }
  },
  methods: {
    // 查询发动机品牌
    getEngineBrandList() {
      this.$api.operation.GetMaintenanceGensetEngineBrand().then((res) => {
        if (res.Code === 1) {
          const brandList = res.Data.map((item) => {
            return {
              value: item.EngineBrandId,
              label: item.BrandName
            }
          })
          this.$set(this.dropdown, 'model', brandList)
        }
      })
    },
    // 查询系列
    getEngineSeriesList() {
      this.$api.operation
        .GetMaintenanceGensetEngineSeries({}, { EngineBrandId: this.form.EngineBrandId })
        .then((res) => {
          if (res.Code === 1) {
            const seriesList = res.Data.map((item) => {
              return {
                value: item.EngineSeriesId,
                label: item.SeriesName
              }
            })
            this.$set(this.dropdown, 'seriesList', seriesList)
          }
        })
    },
    EngineBrandChange() {
      console.log('EngineBrandChange', this.form.EngineBrandId)
      if (this.form.EngineBrandId) {
        this.getEngineSeriesList()
      }
      this.$set(this.form, 'EngineSeriesId', '')
    },

    tableDataChange(val) {
      console.log('tableDataChange', val)
      this.$set(this.form, 'StrategyItemList', val.data)
    },
    // 保存
    handleSave() {
      if (!this.form.StrategyName) {
        this.$message({
          type: 'warning',
          message: '请填写维保策略名称!'
        })
        return
      }
      if (!this.form.EngineBrandId) {
        this.$message({
          type: 'warning',
          message: '请选择发动机品牌!'
        })
        return
      }
      if (!this.form.EngineSeriesId) {
        this.$message({
          type: 'warning',
          message: '请选择发动机系列!'
        })
        return
      }
      if (!this.form.BeatLength) {
        this.$message({
          type: 'warning',
          message: '请填写节拍时长!'
        })
        return
      }
      if (!this.form.BeatCount) {
        this.$message({
          type: 'warning',
          message: '请填写周期节拍个数!'
        })
        return
      }
      if (!this.form.CycleCount) {
        this.$message({
          type: 'warning',
          message: '请填写维保周期个数!'
        })
        return
      }
      if (!this.form.Type) {
        this.$message({
          type: 'warning',
          message: '请选择策略主备分类!'
        })
        return
      }
      if (
        !this.form.StrategyItemList ||
        (this.form.StrategyItemList && this.form.StrategyItemList.length == 0)
      ) {
        this.$message({
          type: 'warning',
          message: '请添加维保事项!'
        })
        return
      }
      // if (this.form.StrategyItemList) {
      //   for (let i = 0, len = this.form.StrategyItemList.length; i < len; i++) {
      //     if (this.form.StrategyItemList[i].edit) {
      //       this.$message({
      //         type: 'warning',
      //         message: '维保事项编辑中!'
      //       })
      //       return
      //     }
      //   }
      // }
      console.log(this.form)
      if (this.$route.query && this.$route.query.form) {
        this.editFun()
        return
      }
      this.saveFun()
    },

    saveFun() {
      // 新增策略
      const ItemList = this.form.StrategyItemList.map((item, index) => {
        return {
          ...item,
          OrderBy: index + 1
        }
      })
      const params = {
        ...this.form,
        ItemList: ItemList
      }
      this.$api.operation.AddMaintenanceGensetStrategy(params).then((res) => {
        if (res.Code === 1) {
          console.log(res)
          this.$message({
            type: 'success',
            message: '添加机组维保策略成功!'
          })

          this.$router.push({
            path: '/setting/agreement-management/maintenance-strategy-new'
          })
        }
      })
    },
    editFun() {
      // 编辑策略
      const ItemList = this.form.StrategyItemList.map((item, index) => {
        return {
          ...item,
          OrderBy: index + 1
        }
      })
      const params = {
        ...this.form,
        ItemList: ItemList
      }
      const query = { StrategyId: this.form.StrategyId }
      this.$api.operation.EditMaintenanceGensetStrategy(params, query).then((res) => {
        if (res.Code === 1) {
          console.log(res)
          this.$message({
            type: 'success',
            message: '编辑机组维保策略成功!'
          })
          this.$router.push({
            path: '/setting/agreement-management/maintenance-strategy-new'
          })
        }
      })
    },
    // 取消
    cancel() {
      if (this.$route.query && this.$route.query.form) {
        this.$router.push({
          path: '/setting/agreement-management/maintenance-strategy-new-check',
          query: {
            rawData: this.form
          }
        })
      } else {
        this.$router.back()
      }
    },
    handleBackClick() {
      this.$router.push({
        path: '/setting/agreement-management/maintenance-strategy-new-check',
        query: { rawData: this.form }
      })
    }
  },
  created() {
    // 编辑
    if (this.$route.query && this.$route.query.form) {
      const form = this.$route.query.form // JSON.parse(this.$route.query.form)
      this.getEngineBrandList()
      if (form.EngineBrandId) {
        this.getEngineSeriesList()
      }
      console.log(form)
      this.$set(this, 'form', form)
    } else if (this.$route.query && this.$route.query.list) {
      // 存储策略为新模板
      this.getEngineBrandList()
      const list = this.$route.query.list
      this.$set(this.form, 'StrategyItemList', list)
    } else {
      // 新增
      this.getEngineBrandList()
    }
  }
}
</script>

<style lang="less" scoped>
.maintenance-strategy-add {
  label {
    cursor: text;
    display: flex;
    white-space: nowrap;
    align-items: center;
    span {
      width: 150px;
    }
    &::before {
      min-width: 10px;
      content: '*';
      color: red;
      font-weight: bold;
    }
    &[label-last] {
      position: absolute;
      top: 7px;
    }
  }
  .cancel-btn {
    margin: 0 10px 0 0;
  }
  > .el-row:nth-child(1) {
    height: 50px;
    padding: 0 12px;
    background: #f3f3f3;
    border: 1px solid #e4e4e4;
  }
  > .el-row:nth-child(2) {
    height: 150px;
    padding: 20px 0 0 0;
    .el-col {
      height: 33.33%;
      display: flex;
      // margin-bottom: 8px;
    }
  }
  > .el-row:nth-child(3) {
    height: calc(100% - 150px);
  }
  .base-info {
    /deep/ .el-input-number {
      width: 91.7%;
      margin-right: 8.8%;
      .el-input__inner {
        text-align: left;
      }
    }
    /deep/ .el-select {
      width: 96%;
      margin-right: 4%;
    }
  }
  /deep/ .el-input-number {
    .el-input-number__decrease {
      border: 0;
    }
    .el-input-number__increase {
      border: 0;
    }
  }
}
.theme-dark .maintenance-strategy-add {
  > .el-row:nth-child(1) {
    background: transparent;
  }
}
</style>



